import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getRoomTypes, deleteRoomTypes } from 'services/roomType';

class GroundworkTrainingPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
    };
  }

  handleDelete = async id => {
    await deleteRoomTypes({ id });
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Tn phng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/room-type/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card>
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/room-type/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData reload={reload} service={getRoomTypes} columns={columns} rowKey="id" />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <GroundworkTrainingPage />
  </Page>
);



